var pages = 0;
export default {
	data() {
		return {
			datas: '',
		}
	},
	onLoad(options) {
		var that = this;
	},
	onShow() {
		var that = this;
		that.getAddress();
	},
	methods: {
		set_address_id(e) {
			var that = this;
			var id = e.currentTarget.id;
			uni.setStorageSync('address_id', id)
			uni.navigateBack({})
		},
		getAddress() {
			var that = this;
			that.pages = 0;
			that.datas = '';
			this.$http.post("address/getAddressList", {
				pages: that.pages,
				a: 1
			}).then((res) => {

				console.log('获取收货地址')
				console.log(res.data);
				that.datas = res.data;
				that.pages = that.pages + 1;

			})
		},
		jumpTo(e) {
			var jumptype = e.currentTarget.dataset.jumptype;
			var url = e.currentTarget.dataset.url;
			var closepage = e.currentTarget.dataset.closepage;
			this.jumpFunction(jumptype, url, closepage);
		},
		choosewxaddress(e) {
			var that = this;
			that.addFormID(e.detail.formId);
			uni.getSetting({
				success(res) {
					console.log(res)
					if (res.authSetting['scope.address'] == false) {
						console.log('error')
						uni.openSetting({
							success(res) {
								console.log(res.authSetting)
							}
						})
					}
				}
			})
			uni.chooseAddress({
				success: function(res) {
					that.$http.post("address/addAddress", {
						phone: res.telNumber,
						addtype: 1
					}, {
						headers: {
							'content-type': 'application/x-www-form-urlencoded',
							'name': encodeURI(res.userName),
							'province': encodeURI(res.provinceName),
							'city': encodeURI(res.cityName),
							'area': encodeURI(res.countyName),
							'address': encodeURI(res.detailInfo),
							'moren': false,
						},
					}).then((res) => {
						console.log(res.data)
						that.getAddress();
					})

				},
				fail: function() {
					console.log('获取失败')

				}
			})
		},
	}
}
